.Search {
    background:rgb(136, 97, 97);
    display:inline-flex;
    padding: 5px;
}

.Search {
    vertical-align: middle;
    flex-direction: row;
    align-items: center;
    border-right-color: yellow;
}

.Search > :first-child input {
    /* border:0px; */
    border-bottom: 2px;
    border-bottom-color:rgb(26, 19, 19);
    border-top-color:rgb(136, 97, 97);
    border-left-color:rgb(136, 97, 97);
    border-right-color:inherit;
    /* border-collapse: collapse; */
    border-style: solid;
    background:inherit;

}

.Search > :last-child{
    margin: 0 0 0 20px
}

.Search > :last-child >  button {
    background-color: red;
    color: white;
    padding: 8px;
    border: 0em;
    border-radius: 1em;
}